.sale-item:hover,
.sale-item:focus {
  box-shadow: 3px 3px 20px rgba(50, 50, 50, 0.4); }

.sale-item:active {
  box-shadow: 3px 3px 20px rgba(28, 98, 205, 0.25); }

.sale-item__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.sale-item__info {
