.sale-item__price-old::after {
  content: "";
  position: absolute;
  top: 8px;
  right: -10px;
  width: 8px;
  height: 10px;
  background-image: url(../img/banner-section/icon-price-old.svg); }

.sale-item__img {
  display: block;
  margin-right: auto;
  margin-left: auto; }

.sale-item__title {
  margin-right: 7px;
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  text-align: center; }

.sale-item__footer {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 25px;
  padding-right: 20px;
  padding-bottom: 25px;
  padding-left: 20px;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  background-color: #f0f0f4; }

.sale-item__footer span {
  display: block;
  font-family: "Barlow", "Helvetica", serif;
  font-weight: 700;
  color: #1c62cd; }

/* =====================================================================================
Search */
