.menu__list {
  display: flex;
  align-items: center;
}

.menu__list-item + .menu__list-item {
  margin-left: 50px;
}

.menu__list-link {
  outline: none;
}

.menu__list-link:hover,
.menu__list-link:focus {
  color: $color-denim;
}

.menu__list-link:active {
  color: rgba($color-denim, 0.7);
}
